.header {
  font-family: "Intro";
  font-size: 20px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: 5px;
  text-align: justify;
  color: #babfc8;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  justify-items: center;
  -ms-flex-align: center;
      align-items: center;
  padding: 0 10px;
  height: 45px;
  background-color: rgba(10, 10, 10, 0.96);
  -webkit-box-shadow: 0 0 1px 0 rgba(0, 0, 0, 0.8);
          box-shadow: 0 0 1px 0 rgba(0, 0, 0, 0.8);
}
.header > img {
  margin-right: 10px;
  margin-left: 10px;
}
.header > img:not(:first-child) {
  cursor: pointer;
}

.light-theme .header {
  color: black;
  background-color: white;
}



