  height: 15px;
  font-family: OpenSans;
  font-size: 18px;
  font-weight: 800;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: justify;
  color: #5c636f;
  font-weight: bold;
  padding: 12px;
  padding-bottom: 0px;
}
.range-handle {
  width: 2.5px;
  background-color: #9fa6b7;
  height: 64px;
  position: absolute;
  cursor: col-resize;
}
.bars-container {
  height: 64px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: end;
      align-items: flex-end;
}

.chart-range-picker-overlay {
  position: absolute;
  height: 64px;
  background-color: #0000005d;
}
.chart-range-picker-container-labels {
  color: #aaaaaa;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  font-family: "OpenSans", san-serif;
  font-size: 0.8rem;
  white-space: nowrap;
}

.chart-range-picker-container {
  margin: 10px 20px 0;
}

.chart-range-picker-container-labels {
  margin-left: -20px;
  margin-right: -20px;
}

/* BAR CHART RANGE */
.bar-chart-range__bar {
  float: left;
  min-height: 1px;
}
.bar-chart-range__bar-fill {
  background-color: #9fa6b7;
  margin-left: 1.5px;
  margin-right: 1.5px;
  height: 100%;
  -webkit-transition: opacity 0.2s linear;
  -o-transition: opacity 0.2s linear;
  transition: opacity 0.2s linear;
}

.bar-chart-range .input-range__slider {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  width: 2.5px;
  background-color: #9fa6b7;
  height: 64px;
  cursor: pointer;
  display: block;
  margin-left: -1.5px;
  border: none;
  margin-top: -0.65rem;
  outline: none;
  position: absolute;
  margin-top: -64px;
  border-radius: 0;
  cursor: col-resize;
  -webkit-transition: -webkit-transform 0.3s ease-out, -webkit-box-shadow 0.3s ease-out;
  transition: -webkit-transform 0.3s ease-out, -webkit-box-shadow 0.3s ease-out;
  -o-transition: transform 0.3s ease-out, box-shadow 0.3s ease-out;
  transition: transform 0.3s ease-out, box-shadow 0.3s ease-out;
  transition: transform 0.3s ease-out, box-shadow 0.3s ease-out, -webkit-transform 0.3s ease-out, -webkit-box-shadow 0.3s ease-out;
}
.bar-chart-range .input-range__slider:active {
  -webkit-transform: scale(1.3);
      -ms-transform: scale(1.3);
          transform: scale(1.3);
}
.bar-chart-range .input-range__slider:focus {
  -webkit-box-shadow: 0 0 0 5px rgba(63, 81, 181, 0.2);
          box-shadow: 0 0 0 5px rgba(63, 81, 181, 0.2);
}
.bar-chart-range .input-range--disabled .input-range__slider {
  background: #3c424f;
  border: 1px solid #3c424f;
  -webkit-box-shadow: none;
          box-shadow: none;
  -webkit-transform: none;
      -ms-transform: none;
          transform: none;
}

.bar-chart-range .input-range__slider-container {
  -webkit-transition: left 0.3s ease-out;
  -o-transition: left 0.3s ease-out;
  transition: left 0.3s ease-out;
}

.bar-chart-range .input-range__label {
  color: #aaaaaa;
  font-family: "OpenSans", san-serif;
  font-size: 0.8rem;
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
  white-space: nowrap;
}

.bar-chart-range .input-range__label--min,
.bar-chart-range .input-range__label--max {
  bottom: -1.4rem;
  position: absolute;
}

.bar-chart-range .input-range__label--min {
  left: 0;
}

.bar-chart-range .input-range__label--max {
  right: 0;
}

.bar-chart-range .input-range__label--value {
  position: absolute;
  top: -1.8rem;
}

.bar-chart-range .input-range__label-container {
  left: -2.5px;
  bottom: -32px;
  position: relative;
}
.input-range__label--max .input-range__label-container {
  left: 0;
}

.bar-chart-range .input-range__track {
  background: #3c424f;
  border-radius: 0rem;
  cursor: pointer;
  display: block;
  height: 0rem;
  position: relative;
  -webkit-transition: left 0.3s ease-out, width 0.3s ease-out;
  -o-transition: left 0.3s ease-out, width 0.3s ease-out;
  transition: left 0.3s ease-out, width 0.3s ease-out;
}
.bar-chart-range .input-range--disabled .input-range__track {
  background: #3c424f;
}

.bar-chart-range .input-range__track--background {
  left: 0;
  margin-top: 0;
  position: absolute;
  right: 0;
  top: 0;
}

.bar-chart-range .input-range__track--active {
  background: #9fa6b7;
}

.bar-chart-range .input-range {
  height: 1rem;
  position: relative;
  width: 100%;
}
.light-theme .bars-container{
  background-color: #ffffff;
  border: 1px solid #ced2dd;
}
.light-theme .chart-range-picker-overlay__left{
   background-color: white;
   border: 1px solid #ced2dd;
}
.light-theme .chart-range-picker-container-labels{
    color: #5c636f;
}
.light-theme .bar-chart-range .input-range__label{
  color: #5c636f;
}



