.bar-chart-container {
}
.bar-chart-container__bar {
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
  opacity: 0.8;
  height: 28px;
  border-radius: 2px;
  margin: 2px 0;
  background-color: #3c424f;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding-left: 10px;
  color: #9fa6b7;
  -webkit-transition: color 0.15s;
  -o-transition: color 0.15s;
  transition: color 0.15s;
  font-family: "OpenSans", san-serif;
  font-weight: bold;
  overflow: visible;
  white-space: nowrap;
  cursor: pointer;
}

.bar-chart-container__bar:hover {
  /*background-image: linear-gradient(to top, #7f8caa, #9fa6b7);*/
  color: #ffffff;
}

.bar-chart-container_colour__bar {
  opacity: 0.7;
  color: lightgray;
}

.bar-chart-container_clicked__bar {
  opacity: 1;
  -webkit-filter: grayscale(0%);
          filter: grayscale(0%);
  /*background-image: linear-gradient(to top, #7f8caa, #9fa6b7);*/
  color: #ffffff;
}
.light-theme.bar-chart-container__bar:hover{
  background-color: transparent;
}
.light-theme .bar-chart-container__bar{
  border-radius: 2px;
  border: solid 1px #ced2dd;
  background-color: #ced2dd;
  color: black;
}


