.statistics-toggle-bar {
  cursor: pointer;
  height: 30px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  position: absolute;
  width: 100%;
  background-color: #000000;
  border-color: #000000;
  font-family: OpenSans;
  font-size: 18px;
  font-weight: 800;
  color: #5c636f;
  z-index: 40;
  bottom: 330px;
  -webkit-transition: 0.1s bottom;
  -o-transition: 0.1s bottom;
  transition: 0.1s bottom;
}
.light-theme .statistics-toggle-bar {
  background-color: white;
  color: black;
}
.toggle-bar-right-items {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  font-size: 14px;
  width: 30%;
}
.toggle-bar-left-items {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  font-size: 14px;
  float: left;
  width: 70%;
}



